<template>
  <component :is="layout" />
</template>

<script>
import { computed, defineAsyncComponent } from 'vue'
import { useRoute } from 'vue-router'
import { useViewport } from '@/shared/utils/useViewport'

export default {
  name: 'AppLayout',
  components: {
    DefaultLayout: defineAsyncComponent(() => import('@/shared/layouts/DefaultLayout/DefaultLayout')),
    Admin: defineAsyncComponent(() => import('@/shared/layouts/Admin')),
    Login: defineAsyncComponent(() => import('@/shared/layouts/Login'))
  },
  setup () {
    const route = useRoute()
    useViewport()

    const layout = computed(() => {
      if (route.meta?.layout) {
        return route.meta.layout
      }

      return 'DefaultLayout'
    })

    return { layout }
  }
}
</script>
